/*옵션선택폼*/
.option-wrap{
	position: relative;
	width: 100%;
	margin-bottom: 10px;
}
.option-select{
	width: 150px;
	height: 35px;
	line-height: 35px;
	padding-left: 15px;
	border: 1px solid #ededed;
	border-radius:5px;
	cursor: pointer;
}
.option-select .option-ico{
	display: inline-block;
	float: right;
	width: 30px;
	height: 35px;
	background: #f0f3f5;
	text-align: center;
	box-sizing: border-box;

}
.option-select .option-ico img{
	width: 15px;
	vertical-align: middle;
}
.option-list-wrap-banks{
	display: none;
	position: absolute;
	z-index: 400;
	top: 35px;
	left: 0;
	width: 150px;
	height: auto;
	padding-bottom: 10px;
	background: #fff;
	border: 1px solid #f0f3f5;

}
.visible{
	display: block;
}
.option-list-wrap-banks .option-list-box{
	float: left;
	padding: 10px 10px 0;
	box-sizing:border-box;
}
.option-list-wrap-banks .option-list-box .option-title{
	display: block;
	font-size: 16px;
	margin-bottom: 10px;
}
.option-list-wrap-banks .option-list-box .option-list{
	padding: 0;
	margin-bottom: 0;
}
.option-list-wrap-banks .option-list-box .option-list li{
	list-style: none;
	margin-bottom: 5px;
}
.option-list-wrap-banks .option-list-box .option-list li input{
	margin-right: 5px;
}
.option-list-wrap-banks .btn-close-wrap{
	padding-right: 10px;
}
.option-list-wrap-banks .btn-close-wrap input{
	float: right;
	height: 30px;
	border: 1px solid #f0f3f5;
	border-radius: 3px;
}
